webpackJsonp([62025924127702],{

/***/ 834:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<h1 id=\"victoryclipcontainer\"><a href=\"#victoryclipcontainer\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>VictoryClipContainer</h1>\n<p><code class=\"language-text\">VictoryClipContainer</code> is a specialized group container that enables curtain-style transitions for continuous data types like <code class=\"language-text\">VictoryLine</code> and <code class=\"language-text\">VictoryArea</code>. <code class=\"language-text\">VictoryClipContainer</code> will render its children either in a regular <code class=\"language-text\">&lt;g&gt;</code> element, or in a <code class=\"language-text\">&lt;g&gt;</code> element clipped by a rectangular clip path when a <code class=\"language-text\">clipWidth</code> is supplied.</p>\n<h2 id=\"props\"><a href=\"#props\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Props</h2>\n<h3 id=\"children\"><a href=\"#children\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>children</h3>\n<p><code class=\"language-text\">type: element || array[element]</code></p>\n<p><code class=\"language-text\">VictoryClipContainer</code> renders a single child, or an array of children in group element.</p>\n<h3 id=\"circlecomponent\"><a href=\"#circlecomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>circleComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">circleComponent</code> specifies the element to use when a <code class=\"language-text\">VictoryClipContainer</code> renders a circular clip path. By default, <code class=\"language-text\">VictoryClipContainer</code> uses the <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives#circle\">Circle component</a>.</p>\n<p><em>default:</em> <code class=\"language-text\">&lt;Circle/&gt;</code></p>\n<h3 id=\"classname\"><a href=\"#classname\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>className</h3>\n<p><code class=\"language-text\">type: string</code></p>\n<p>The <code class=\"language-text\">className</code> prop specifies a class name that will be applied to the rendered element.</p>\n<p><em>example:</em> <code class=\"language-text\">className=&quot;myClipPath&quot;</code></p>\n<h3 id=\"clipheight\"><a href=\"#clipheight\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>clipHeight</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">clipHeight</code> prop determines the base height of the rectangular clip path. This prop should be\ngiven as a number. If this prop is not given, it will be calculated based on the height and padding\nof the parent chart.</p>\n<h3 id=\"clipid\"><a href=\"#clipid\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>clipId</h3>\n<p><code class=\"language-text\">type: string || number</code></p>\n<p>The <code class=\"language-text\">clipId</code> prop may be used to set a deterministic id for the container. When a <code class=\"language-text\">containerId</code> is not manually set, a unique id will be generated. It is usually necessary to set deterministic ids for automated testing.</p>\n<h3 id=\"clippadding\"><a href=\"#clippadding\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>clipPadding</h3>\n<p><code class=\"language-text\">type: { top: number, bottom: number, left: number, right: number }</code></p>\n<p>The <code class=\"language-text\">clipPadding</code> prop is used when the clipped area should be larger than the range of a chart.\nThis prop should be given as an object with <code class=\"language-text\">top</code>, <code class=\"language-text\">bottom</code>, <code class=\"language-text\">left</code>, and <code class=\"language-text\">right</code> properties. Set\nthe <code class=\"language-text\">clipPadding</code> prop is useful for extending the visible area of a chart in some dimension so\nthat data or labels are not cut off.</p>\n<h3 id=\"clippathcomponent\"><a href=\"#clippathcomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>clipPathComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">clipPathComponent</code> prop specifies the clip path to apply to the rendered group when appropriate. By default, <code class=\"language-text\">VictoryClipContainer</code> uses the <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives#clippath\">ClipPath component</a>.</p>\n<p><em>default:</em> <code class=\"language-text\">&lt;ClipPath/&gt;</code></p>\n<h3 id=\"clipwidth\"><a href=\"#clipwidth\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>clipWidth</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">clipWidth</code> prop determines the base width of the rectangular clip path. This prop should be\ngiven as a number. If this prop is not given, it will be calculated based on the width and padding\nof the parent chart.</p>\n<h3 id=\"events\"><a href=\"#events\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>events</h3>\n<p><code class=\"language-text\">type: object</code></p>\n<p>The <code class=\"language-text\">events</code> prop attaches arbitrary event handlers to the group element. This prop should be given as an object of event names and corresponding event handlers. When events are provided via Victory’s event system, event handlers will be called with the event, the props of the component it is attached to, and an <code class=\"language-text\">eventKey</code> when applicable.</p>\n<p><em>examples:</em> <code class=\"language-text\">events={{onClick: (evt) =&gt; alert(&quot;x: &quot; + evt.clientX)}}</code></p>\n<h3 id=\"groupcomponent\"><a href=\"#groupcomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>groupComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p><code class=\"language-text\">VictoryClipContainer</code> uses the standard <code class=\"language-text\">groupComponent</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#groupcomponent\">Read about it here</a></p>\n<p><em>default:</em> <code class=\"language-text\">&lt;g/&gt;</code></p>\n<h3 id=\"origin\"><a href=\"#origin\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>origin</h3>\n<p><code class=\"language-text\">type: { x: number, y: number }</code></p>\n<p>Victory components will pass an <code class=\"language-text\">origin</code> prop is to define the center point in svg coordinates for polar charts. <strong>This prop should not be set manually.</strong></p>\n<h3 id=\"polar\"><a href=\"#polar\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>polar</h3>\n<p><code class=\"language-text\">type: boolean</code></p>\n<p>Victory components can pass a boolean <code class=\"language-text\">polar</code> prop to specify whether a label is part of a polar chart. <strong>This prop should not be set manually.</strong></p>\n<h3 id=\"radius\"><a href=\"#radius\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>radius</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">radius</code> prop determines the radius of the circular clip path used for polar charts. This prop should be\ngiven as a number. If this prop is not given, it will be calculated based on the dimensions and padding\nof the parent chart.</p>\n<h3 id=\"rectcomponent\"><a href=\"#rectcomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>rectComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">circleComponent</code> specifies the element to use when a <code class=\"language-text\">VictoryClipContainer</code> renders a rectangular clip path. By default, <code class=\"language-text\">VictoryClipContainer</code> uses the <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives#rect\">Rect component</a>.</p>\n<p><em>default:</em> <code class=\"language-text\">&lt;Rect/&gt;</code></p>\n<h3 id=\"translatex\"><a href=\"#translatex\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>translateX</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">translateX</code> prop determines the offset of the clip path from the base x coordinate. This prop\nshould be given as a number. If this prop is not given, it will be calculated based on the padding\nof the parent chart.</p>\n<h3 id=\"translatey\"><a href=\"#translatey\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>translateY</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">translateY</code> prop determines the offset of the clip path from the base y coordinate. This prop\nshould be given as a number. If this prop is not given, it will be calculated based on the padding\nof the parent chart.</p>","frontmatter":{"id":10,"scope":null,"title":"VictoryClipContainer"},"fields":{"slug":"/docs/victory-clip-container/"}}},"pathContext":{"slug":"/docs/victory-clip-container/"}}

/***/ })

});


